@import "@/assets/global.scss";
















































































.main-content {
  box-sizing: border-box;
  margin-top: 16px;
  padding-left: 16px;
  padding-right: 16px;
}

.tab {
  display: flex;
  margin-bottom: 20px;

  > div {
    @extend .font-16-bold;
    height: 38px;
    padding: 7px 15px;
    margin-right: 10px;
    border: 1px solid #FCA311;
    box-sizing: border-box;
    border-radius: 8px;
    background: #FFFFFF;
    color: #FCA311;
    cursor: pointer;
  }

  > div.selected {
    background: #FCA311;
    color: #FFFFFF;
  }
}
