@import "@/assets/global.scss";



































.main {
  width: 100%;
  height: 98px;
  margin-bottom: 16px;
  background: #FFFFFF;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  display: flex;
}

.content {
  width: calc(100% - 72px);
  padding: 16px;
  text-align: left;
}

.status {
  @extend .font-14-bold;
  width: 72px;
  color: #BDBDBD;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0 10px 10px 0;
  user-select: none;

  &.use {
    background: #FCA311;
    color: #FFFFFF;
  }
}

.expired-date {
  color: #000000;
  opacity: 0.4;
}
